<template>
<nav-bar>
  <span slot="title" class="title">{{product.name}}</span>
</nav-bar>
<table class="table">
  <tr>
    <td style="width:200px;padding-left:50px;">
      <img @click="inputFileClick" alt="店铺头像" class="img-circle" width="100" height="100" v-bind:src="product.avatar" />
      <input @change="uploadAvatar" v-el:avatar class="avatar" type="file" accept="image/*" style="display:none;" />
    </td>
    <td colspan="2">
      <div class="content-form">
        <form @submit="update" enctype="multipart/form-data" accept-charset="UTF-8" method="post">
          <div class="form-group row">
            <label for="inputEmail3" class="col-sm-2 form-control-label">商品名称</label>
            <div class="col-sm-10">
              <input class="form-control" placeholder="名称" type="text" v-model="product.name" />
            </div>
          </div>
          <div class="form-group row">
            <label for="inputPassword3" class="col-sm-2 form-control-label">商品价钱</label>
            <div class="col-sm-10">
              <input class="form-control" placeholder="价钱" type="text" v-model="product.price" />
            </div>
          </div>
          <div class="form-group row">
            <label for="inputPassword3" class="col-sm-2 form-control-label">所属分类</label>
            <div class="col-sm-10">
              <select class="c-select" v-model="product.category_id">
                <option v-for="category in categories" track-by="$index" v-bind:value="category.id">
                  {{category.name}}
                </option>
              </select>
            </div>
          </div>
          <div class="form-group row">
            <label for="inputEmail3" class="col-sm-2 form-control-label"></label>
            <div class="col-sm-10">
              <input type="submit" name="commit" value="保 存" class="btn btn-primary" />
            </div>
          </div>
        </form>
      </div>
    </td>
  </tr>
  <tr>
    <td colspan="2">当年月销售量分析</td>
  </tr>
  <tr v-if="chartData.datasets[0].data.length > 0">
    <td colspan="2" rowspan="11">
      <div style="width:90%;height:100%;">
      </div>
    </td>
    <td width="200">最高月份</td>
  </tr>
  <tr>
    <td>￥{{maxMonth}}</td>
  </tr>
  <tr>
    <td>最低月份</td>
  </tr>
  <tr>
    <td>￥{{minMonth}}</td>
  </tr>
  <tr>
    <td>月份平均</td>
  </tr>
  <tr>
    <td>￥{{averageByYear}}</td>
  </tr>
  <tr>
    <td>当年总额</td>
  </tr>
  <tr>
    <td>￥{{amountByYear}}</td>
  </tr>
  <tr>
    <td></td>
  </tr>
  <tr>
    <td></td>
  </tr>
</table>
</template>
<script>
  export default {
    data () {
      return {
        categories: {},
        product: {},
        chartData: {
          labels: ['1月', '2月', '3月', '4月', '5月', '6月', '7月', '8月', '9月', '10月', '11月', '12月'],
          datasets: [
            {
              label: 'My Second dataset',
              fillColor: 'rgba(151,187,205,0.2)',
              strokeColor: 'rgba(151,187,205,1)',
              pointColor: 'rgba(151,187,205,1)',
              pointStrokeColor: '#fff',
              pointHighlightFill: '#fff',
              pointHighlightStroke: 'rgba(151,187,205,1)',
              data: []
            }
          ]
        }
      }
    },
    route: {
      data (transition) {
        this.$http.get('http://jiancan.me/api/m1/shops/categories.json', { access_token: localStorage.token }).then(function (response) {
          this.$set('categories', response.data)
        }, function (response) {
          this.$dispatch('response-msg', response)
        })
        var params = this.$route.params
        this.$http.get('http://jiancan.me/api/m1/products/one.json', { access_token: localStorage.token, product_id: params.product_id }).then(function (response) {
          this.$set('product', response.data)
        }, function (response) {
          this.$dispatch('response-msg', response)
        })
        this.$http.get('http://jiancan.me/api/m1/products/statistic.json', { access_token: localStorage.token, product_id: params.product_id }).then(function (response) {
          this.$set('chartData.datasets[0].data', response.data.products)
        }, function (response) {
          this.$dispatch('response-msg', response)
        })
      }
    },

    computed: {
      maxMonth () {
        return Math.max(...this.chartData.datasets[0].data)
      },
      minMonth () {
        return Math.min(...this.chartData.datasets[0].data)
      },
      averageByYear () {
        var length = this.chartData.datasets[0].data.length
        var sum = 0.0
        if (length > 0) {
          for (var x = 0; x < length; x++) {
            sum = sum + Number(this.chartData.datasets[0].data[x])
          }
          return (sum / length).toFixed(2)
        }
        return 0.0
      },
      amountByYear () {
        var length = this.chartData.datasets[0].data.length
        var sum = 0.0
        if (length > 0) {
          for (var x = 0; x < length; x++) {
            sum = sum + Number(this.chartData.datasets[0].data[x])
          }
          return sum
        }
        return 0.0
      }
    },
    methods: {
      inputFileClick (e) {
        return this.$els.avatar.click()
      },
      uploadAvatar (e) {
        e.preventDefault()

        if (this.$els.avatar.value === '') return
        var formData = new FormData()
        formData.append('access_token', localStorage.token)
        formData.append('product_id', this.$route.params.product_id)
        formData.append('product[avatar]', this.$els.avatar.files[0])

        this.$http.put('http://jiancan.me/api/m1/products/one.json', formData).then(function (response) {
          this.product = response.data.product
        }, function (response) {
          this.$dispatch('response-msg', response)
        })
      },
      update (e) {
        e.preventDefault()

        var formData = new FormData()
        formData.append('access_token', localStorage.token)
        formData.append('product_id', this.$route.params.product_id)
        formData.append('product[name]', this.product.name)
        formData.append('product[price]', this.product.price)
        formData.append('product[category_id]', this.product.category_id)

        this.$http.put('http://jiancan.me/api/m1/products/one.json', formData).then(function (response) {
          this.product = response.data.product
        }, function (response) {
          this.$dispatch('response-msg', response)
        })
      }
    },
    components: {
      'nav-bar': require('../components/nav-bar.vue')
    }
  }
</script>



